<template>
    <div class="skill" v-if='info'>
        <p class="content-title">{{title}}</p>
        <div class="content-container">
            <div class="skill-circle" ref='skillCircle'>
                <div class="circle-list">
                    <div class="bull-warp" ref='bullWarp'>
                        <span class="circle-item"
                        v-for='(item, index) in skill'
                        :key='index'
                        :style="rotateStyle(index)"
                        @click='showSkill(index)'
                        ref='bulls'
                        :class="{'active':index===bullIndex}"
                        >
                            {{item.title}}
                        </span>
                    </div>
                    <div class="center-circle">
                        <div class="content" v-for='(item, index) in skill' :key='index' :class="{'active':index===bullIndex}">
                            {{item.title}}:{{item.scale}}%
                        </div>
                    </div>
                </div>
            </div>
            <transition
            appear
            appear-class="before"
            appear-to-class="after"
            appear-active-class="appear-active"
            >
                <div class="skill-sketch-warp">
                    <p class="skill-sketch" v-for="item in info" :key="item.id">{{item.title}}</p>
                </div>
            </transition>
            <ul class="skill-list">
                <li v-for="(item, index) in skill" :key="item.id">
                    <span>{{item.scale}}%</span>
                    <p>
                        <span>{{item.title}}</span>
                        <transition appear @appear="customAppearHook">
                            <em :data-index='index'></em>
                        </transition>
                    </p>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import { setTimeout, setInterval, clearInterval, clearTimeout } from 'timers'
const url = 'https://www.easy-mock.com/mock/5c9b940ca88cf9475e399780/resume/skill'
export default {
    data() {
        return {
            info: [],
            skill: [],
            title: '',
            deg: 0,
            bullIndex: 0,
            timer: {
                one: null,
                two: null
            }
        }
    },
    created() {
        this.$http(url).then(({ data }) => {
            this.info = data.data.titleList
            this.skill = data.data.skill
            this.title = data.data.title
        }).catch((error) => {
            console.log(error)
        })
    },
    mounted() {
        this.$nextTick(() => {
            this.timer.one = setInterval(this.autoRotate, 2000)
        })
    },
    watch: {
        bullIndex() {
            const display = getComputedStyle(this.$refs.skillCircle).display
            if (this.$refs.bullWarp && display === 'block') {
                this.$refs.bullWarp.style.transform = this.$refs.bullWarp.style.webkitTransform = `rotate(${this.deg}deg)`
                this.$refs.bulls.forEach(bull => {
                    bull.style.transform = bull.style.webkitTransform = `rotate(${-this.deg}deg)`
                })
            }
        }
    },
    methods: {
        customAppearHook(el) {
            setTimeout(() => {
                el.style.width = this.skill[el.dataset.index].scale + '%'
            }, 50)
        },
        rotateStyle(index) {
            const bullWarp = this.$refs.bullWarp
            const scale = 360 / this.skill.length
            const y = Math.cos(scale * index * Math.PI / 180) * 170 + bullWarp.offsetHeight / 2
            const x = Math.sin(scale * index * Math.PI / 180) * 170 + bullWarp.offsetWidth / 2
            return `top:${y}px;left:${x}px`
        },
        autoRotate() {
            if (this.deg >= 360) {
                this.deg = 0
            }
            this.deg += 360 / this.skill.length
            this.bullIndex = this.deg % 360 / 60
        },
        showSkill(index) {
            this.bullIndex = index
            this.deg = 60 * this.bullIndex
            clearTimeout(this.timer.two)
            this.timer.two = setTimeout(() => {
                clearInterval(this.timer.one)
                this.timer.one = setInterval(this.autoRotate, 3000)
            }, 1000)
        }
    }
}
</script>

<style lang='less' scoped>
@import '../common/style/common.less';
@color:#43c94e;
.emColor(@index,@el) when(@index>0){
    .emColor(@index - 1,@el);
    @spincolor:spin( @color, (180 - @index * 50) );
    @{el}{
        &:nth-last-of-type(@{index}){
            em{
                background: @spincolor;
            }
        }
    }
}
.bull(@index,@el) when(@index>0){
    .bull(@index - 1,@el);
    @spincolor:spin( @color, (180 - @index * 50) );
    @{el}{
        &:nth-last-of-type(@{index}){
            border: 2px solid @spincolor;
            background-color: @spincolor;
        }
    }
}
.contentBull(@index) when(@index>0){
    .contentBull(@index - 1);
    @spincolor:spin( rgba(26,188,156,.8), (180 - @index * 50) );
    .content{
        &:nth-last-of-type(@{index}){
            background-color: @spincolor;
        }
    }
}
.before{
    opacity: 0;
    transform: scale(0)
}
.after{
    opacity: 1;
    transform: scale(1)
}
.appear-active{
    transition: 1s;
}
.content-title{
    animation-duration: .9s;
    color: #fff;
    &::after{
        background: #fff;
    }
}
.skill{
    .position;
    background: url(../assets/bg/bg.png) #4d5e8f;
}
.content-container{
    display: flex;
    flex-direction: column;
}
.skill-circle{
    display: none;
    font-family: '微软雅黑';
    flex: 0 0 50%;
   .circle-list{
       left: -50px;
       top: -50px;
       width: 450px;
       height: 450px;
       position: relative;
       .center-circle{
           position: absolute;
           left: 50%;
           top: 50%;
           width: 180px;
           height: 180px;
           margin: -90px 0  0 -90px;
           border-radius: 50%;
           background: url(../assets/bgcircle.png) no-repeat;
           background-size: cover;
           box-shadow: 0px 0px 69px 1px #2b152e;
           .contentBull(6);
           .content{
                .position;
                opacity: 0;
                transition: 0.5s;
                border-radius: 50%;
                line-height: 180px;
                text-align: center;
                font-size: 21px;
                transform: scale(0);
                color: #fff;
                &.active{
                    opacity: 1;
                    transform: scale(1);
                }
                &::before{
                    content: '';
                    width: 50%;
                    height: 100%;
                    border-bottom-left-radius: 250px;
                    border-top-left-radius: 250px;
                    position: absolute;
                    top: 0;
                    left: 0;
                    background: rgba(0, 0, 0, 0.22);
                    z-index: -1;
                }
           }
        }
        .bull-warp{
            transition: 1s ease-in-out;
            .bull(6,span);
            height: 100%;
            .circle-item{
                line-height: 70px;
                text-align: center;
                font-size: 12px;
                cursor: pointer;
                position: absolute;
                margin: -40px 0 0 -40px;
                top: 50%;
                left: 50%;
                width: 80px;
                height: 80px;
                border-radius: 50%;
                background: #fff;
                padding: 5px;
                background-clip: content-box;
                box-sizing: border-box;
                transition: .4s background-color,.4s color,.4s border-color;
                box-shadow: 0px 0px 20px 1px #2b152e;
                &.active{
                    background-color: #34495e;
                    color: #fff;
                    border-color: #334444;
                }
                &:hover{
                    background-color: #34495e;
                    color: #fff;
                    border-color: #334444;
                }
            }
        }
   }
}
.skill-sketch-warp{
    padding: 0 3%;
    flex: 0 0 40%;
}
.skill-sketch{
    color:#fff;
    font-weight: 600;
    font-family: '微软雅黑';
    text-indent: 2em;
    font-size: 12px*@scale;
    line-height: 25px;
}
.skill-list{
    padding: 0 3%;
    flex: 0 0 100%;
    font-family: '微软雅黑';
    .emColor(7,li);
    li{
        border: none!important;
        padding: 5px 0;
        height: 25px;
        position: relative;
        line-height: 25px;
        >span{
            color: #fff;
            height: 25px;
            position: absolute;
            width: 55px;
            text-align: center;
            background: #000;
            border-radius: 5px 0 0 5px;
        }
        p{
            padding-left: 8px;
            border-radius: 5px;
            background: #bdc3c7;
            height: 100%;
            position: relative;
            margin-left: 55px;
            span{
                position: relative;
                z-index: 999;
            }
            em{
                left: 0px;
                top: 0px;
                width: 0%;
                position: absolute;
                height: 100%;
                transition: 1s width;
            }
        }
    }
}
@media all and (min-width:375px) {
    .skill-sketch{
        font-weight: normal;
        font-size: 14px*@scale;
        line-height: 27px;
    }
    .skill-list{
        li{
             padding: 7px 0;
            height: 30px;
            line-height: 30px;
            >span{
                height: 30px;
            }
        }
    }
}
@media all and (min-width:768px){
    .skill-sketch{
        font-size: 26px;
        line-height: 35px;
    }
    .skill-list{
        li{
            padding: 10px 0;
            height:37px;
            line-height:37px;
            >span{
                font-size:28px;
                height:37px;
                width: 100px;
            }
            p{
                padding-left: 20px;
                border-radius: 10px;
                margin-left: 100px;
                font-size:30px;
            }
        }
    }
}
@media all and (min-width:960px) {
    .skill-circle{
        display: block;
    }
    .skill-sketch-warp{
        padding: 0;
    }
    .skill-list{
        padding: 0;
    }
    .content-container{
        flex-direction: row;
    }
    .skill-list{
        display: none;
    }
    .skill-sketch{
        font-size: 16px;
    }
}
@media all and (min-width:1180px) {
    .skill-sketch-warp{
        padding-top: 30px;
    }
}
</style>
